import axios from 'axios';

const OPENAI_API_URL = 'https://api.openai.com/v1/chat/completions';
const OPENAI_API_KEY = process.env.OPENAI_API_KEY;

const headers = {
  'Content-Type': 'application/json',
  'Authorization': `Bearer ${OPENAI_API_KEY}`
};

export async function getAIResponse(prompt) {
  try {
    const response = await axios.post(OPENAI_API_URL, {
      model: 'gpt-3.5-turbo',
      messages: [
        {
          role: 'system',
          content: 'You are a helpful customer service assistant. Provide clear, concise, and professional responses.'
        },
        {
          role: 'user',
          content: prompt
        }
      ],
      temperature: 0.7,
      max_tokens: 1000,
      top_p: 0.9
    }, {
      headers
    });

    return response.data?.choices?.[0]?.message?.content || 'No response generated';

  } catch (error) {
    console.error('AI Response Error:', error);
    
    if (error.response) {
      console.error('Error data:', error.response.data);
      console.error('Error status:', error.response.status);
      
      if (error.response.status === 401 || error.response.status === 403) {
        return 'Invalid username or password';
      }
      
      return `Error: ${error.response.data?.error?.message || 'Failed to generate response'}`;
    } else if (error.request) {
      return 'Error: No response received from AI service';
    } else {
      return `Error: ${error.message}`;
    }
  }
}

// Alternative simpler approach for single prompts
export async function getSimpleAIResponse(prompt) {
  try {
    const response = await axios.post(OPENAI_API_URL, {
      model: 'gpt-3.5-turbo',
      messages: [
        {
          role: 'system',
          content: 'You are a helpful customer service assistant. Provide clear, concise, and professional responses.'
        },
        {
          role: 'user',
          content: prompt
        }
      ],
      temperature: 0.7,
      max_tokens: 200,
      top_p: 0.9
    }, {
      headers
    });

    const generatedText = response.data?.choices?.[0]?.message?.content || 'No response generated';
    return generatedText;

  } catch (error) {
    console.error('AI Response Error:', error);
    return handleError(error);
  }
}

// Helper function to handle errors consistently
function handleError(error) {
  if (error.response) {
    console.error('Error data:', error.response.data);
    console.error('Error status:', error.response.status);
    
    if (error.response.status === 401 || error.response.status === 403) {
      return 'Invalid username or password';
    }
    
    return `Error: ${error.response.data?.error?.message || 'Failed to generate response'}`;
  } else if (error.request) {
    return 'Error: No response received from AI service';
  } else {
    return `Error: ${error.message}`;
  }
}

// Helper function to format conversation context for ChatGPT
export function formatConversationForDialoGPT(conversation) {
  if (!conversation || !conversation.messages) return { history: [], lastMessage: '' };
  
  const messages = conversation.messages;
  const history = [];
  let lastMessage = '';
  
  // Format messages for ChatGPT
  for (let i = 0; i < messages.length - 1; i += 2) {
    if (messages[i] && messages[i + 1]) {
      history.push(messages[i].content); // User input
      history.push(messages[i + 1].content); // Assistant response
    }
  }
  
  // Get the last message if it's from user
  if (messages.length > 0 && messages[messages.length - 1].sender === 'customer') {
    lastMessage = messages[messages.length - 1].content;
  }
  
  return { history, lastMessage };
}

// Enhanced function to generate response with conversation context
export async function generateResponse(conversation, query) {
  const { history, lastMessage } = formatConversationForDialoGPT(conversation);
  
  // Use the query or last message
  const promptText = query || lastMessage;
  
  if (history.length > 0) {
    // Use conversation history
    return getAIResponse(promptText);
  } else {
    // Simple response without history
    return getSimpleAIResponse(promptText);
  }
}

// Helper function to maintain conversation state
export class ConversationManager {
  constructor() {
    this.conversationHistory = [];
  }
  
  addUserInput(input) {
    this.conversationHistory.push(input);
  }
  
  addBotResponse(response) {
    this.conversationHistory.push(response);
  }
  
  async getResponse(userInput) {
    // Add user input to history
    this.addUserInput(userInput);
    
    // Get AI response
    const aiResponse = await getAIResponse(userInput);
    
    // Add bot response to history
    this.addBotResponse(aiResponse);
    
    return aiResponse;
  }
  
  clearHistory() {
    this.conversationHistory = [];
  }
  
  getHistory() {
    return [...this.conversationHistory];
  }
}